@import 'variables';
@import 'mixins';

.about-cont {
  @include flex-type(center, center, column);
  background-color: #cdcdcd;
  padding: 20px;

  h2 {
    font-size: 3rem;
    font-weight: 800;
    padding: 20px;
    letter-spacing: 5px;
  }

  @include respond-to('md') {
    align-items: flex-end;
    padding: 20px 50px;
  }

  .content {
    @include respond-to('sm') {
      @include flex-type(center, center);
    }

    & > * {
      padding: 20px;

      @include respond-to('md') {
        padding: 20px 40px;
      }
    }
  }

  .img {
    @include flex-type(center, center);
    // perspective: 1000px;

    @include respond-to('sm', 'md') {
      max-width: 300px;
    }

    @include respond-to('md') {
      flex: 0 0 33%;
    }

    img {
      max-width: 300px;
      overflow: auto;
    }
  }

  .basic-info > * {
    margin: 10px 0;

    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }

  .name-title {
    @include flex-type(center, center, column);

    @include respond-to('md') {
      align-items: flex-start;
    }

    * {
      display: block;
      font-style: italic;
      text-align: center;
    }
  }

  .name {
    font-size: 2.5rem;
    line-height: 2.5rem;
  }

  .title {
    font-size: 1.25rem;
    font-weight: 200;
    line-height: 1.5rem;
  }

  .social-icons {
    @include flex-type(center, center);

    @include respond-to('sm') {
      width: unset;
    }

    @include respond-to('md') {
      justify-content: flex-start;
    }

    a {
      @include flex-type(center, center);
      border-radius: 5px;
    }
  }

  .social {
    padding: 5px;

    &:active,
    &:focus,
    &:hover {
      box-shadow: 0 0 5px rgba(0,0,0,0.3);

      &.linkedin svg {
        color: #0077b5;
      }
    }
  }

  .overview {
    padding: 0 20px;
    text-align: justify;

    @include respond-to('md') {
      padding: 0 40px;
    }

    p {
      font-size: 0.8rem;
    }
  }
}